/*    Accessories */

.accessories__filter {
  margin-bottom: 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 24px;
}

.accessories__filter-item {
  display: flex;
}

.accessories__btn {
  padding: 8px 16px;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.18;
  background-color: transparent;

  color: #86e543;

  width: 124px;
  height: 35px;
  border: 1px solid #86e543;
  border-radius: 16px;
  cursor: pointer;
}

.accessories__btn:hover,
.accessories__btn:focus {
  background-color: #86e543;
  color: #01383a;
}

.accessories__title {
  position: relative;
  margin-bottom: 27px;
  text-align: left;
  font-weight: 500;
  font-size: 24px;
  line-height: 1.16;
  color: #ffffff;

  @media screen and (min-width: 768px) {
    font-size: 56px;
    margin-bottom: 51px;
    padding-left: 142px;
  }
}

.accessories__title::after {
  position: absolute;
  content: "";
  border: 1px solid #28780D;
box-shadow: 0px 4px 25px rgba(40, 120, 13, 0.25);
  
}


.accessories__overlay-box {
  position: relative;
  overflow: hidden;
}

.accessories__overlay {
  position: absolute;
  bottom: 0;
  padding: 16px;
  height: 100%;
  background: linear-gradient(0deg, #01383a, #01383a);
  border: 1px solid #86e543;
  box-shadow: 0px 4px 4px rgba(134, 229, 67, 0.2);
  border-radius: 12px;
  transform: translateY(100%);
  transition: transform 250ms linear;
}

.accessories__overlay-text {
  display: block;
  margin-bottom: 14px;
  font-size: 14px;
  line-height: 1.14;
  color: #ffffff;
}

.accessories__item:hover .accessories__overlay,
.accessories__item:focus .accessories__overlay {
  transform: translateY(0);
}

.accessories__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
}

.accessories__item {
  height: 100%;
  margin-bottom: 16px;

  @media screen and (min-width: 768px) {
    width: calc((100% - 80px) / 2);
    gap: 20px;
    margin-bottom: 24px;
  }

  @media screen and (min-width: 1200px) {
    width: calc((100% - 80px) / 4);
  }
}

.accessories__product {
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 22px;
  line-height: 1.18;
  letter-spacing: 0.03em;
  color: #86e543;

  
}

.accessories__prices {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: left;
}

.accessories__first-price {
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  text-decoration-line: line-through;
  color: #8d9289;
}

.accessories__second-price {
  font-weight: 500;
  font-size: 20px;
  line-height: 100%;
  color: #86e543;
}

.accessories__prise {
  font-weight: 600;
  font-size: 14px;
  line-height: 100%;

  text-decoration-line: line-through;

  color: #8d9289;
}

.accessories__sale {
  margin-left: auto;
  font-weight: 600;
  font-size: 20px;
  line-height: 100%;
  text-decoration: none;

  color: #86e543;
}

.accessories__library {
  padding: 8px;
}
