.blog-container {
  @media screen and (min-width: 1280px) {
    display: none;
  }
}

.blog-content {
  @media screen and (min-width: 1280px) {
    display: flex;
    justify-content: space-between;
  }
}

.blog-container-desc {
  @media screen and (max-width: 1279px) {
    display: none;
  }
}

.blog-title {
  font-weight: 600;
  font-size: 24px;
  line-height: 1.16;
  color: #fff;

  @media screen and (min-width: 768px) {
    font-weight: 500;
    font-size: 56px;
    line-height: 1.17;
    margin-left: 59px;
  }

  @media screen and (min-width: 1280px) {
    margin-left: 62px;
  }
}

.blog__photo-title-desc {
  @media screen and (max-width: 1279px) {
    display: none;
  }

  font-weight: 500;
  font-size: 56px;
  line-height: 1;
  color: #fff;
  margin-bottom: 52px;

  max-width: 692px;
}

.blog__photo-title {
  font-weight: 500;
  font-size: 24px;
  line-height: 1.16;
  color: #fff;
  margin-bottom: 16px;

  max-width: 692px;

  @media screen and (min-width: 768px) {
    font-size: 56px;
    line-height: 1;
  }

  @media screen and (min-width: 1280px) {
    display: none;
  }
}

.blog__tab-content {
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    display: flex;
    justify-content: space-between;
  }
}

.blog-photo {
  margin-bottom: 8px;

  @media screen and (min-width: 768px) {
    border-radius: 12px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    margin-bottom: 16px;
  }
}

.blog-container__desc {
  display: flex;
  justify-content: space-between;
}

.blog-text {
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;

  @media screen and (min-width: 768px) {
    font-size: 24px;
  }
}

.blog__text-name {
  color: #fff;

  @media screen and (max-width: 767px) {
    margin-bottom: 8px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    margin-bottom: 32px;
    max-width: 132px;
  }
}

.blog__text-date {
  color: #8d9289;

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    max-width: 252px;
  }

  @media screen and (max-width: 1279px) {
    margin-bottom: 16px;
  }
}

.blog__text-text {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.18;

  color: #fff;
  margin-bottom: 8px;

  @media screen and (min-width: 768px) {
    font-size: 24px;
    line-height: 1.16;
    max-width: 688px;

    margin-bottom: 16px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 692px;
  }
}

.blog__link-container {
  text-align: end;
  margin-bottom: 24px;

  @media screen and (min-width: 768px) {
    margin-bottom: 40px;
  }
}

.blog__text-link {
  color: #86e543;
}
